import React from "react";
import App from "next/app";
import Head from "next/head";
import { ThemeProvider, makeStyles, createStyles } from "@material-ui/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import theme from "../src/theme";
import StickyFooter from "../src/MyStickyFooter";
import ElevateAppBar from "../src/MyElevatedAppBar";
import { Container, Theme } from "@material-ui/core";

const useStyles = makeStyles({
  "@global": {
    body: {
      backgroundImage: "url('https://images.unsplash.com/photo-1453928582365-b6ad33cbcf64?ixlib=rb-1.2.1&auto=format&fit=crop&w=1652&q=80')",
      backgroundRepeat: "no-repeat",
      backgroundPosition: "center center",
      backgroundSize: "cover",
      backgroundAttachment: "fixed",
      height: "100%"
    },
  },
});

class MyApp extends App {

  componentDidMount() {
    // Remove the server-side injected CSS.
    const jssStyles = document.querySelector("#jss-server-side");
    if (jssStyles) {
      jssStyles.parentNode!.removeChild(jssStyles);
    }
  }

  render() {
    useStyles();
    const { Component, pageProps } = this.props;
    /* const { HeaderButtons } = this.state as IState; */
    return (
      <React.Fragment>
        <Head>
          <title>Stefanos Agelastos</title>
        </Head>
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <Container maxWidth="xl">
          <StickyFooter>
              <ElevateAppBar />
              <Component {...pageProps} />
          </StickyFooter>
          </Container>
        </ThemeProvider>
      </React.Fragment>
    );
  }
}

export default MyApp;
